import React, {Component} from 'react';
import { Link } from 'react-router-dom';
import CookieConsent from "react-cookie-consent";

export default class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
        }
    }

    componentDidMount(){
        //alert(JSON.stringify(this.props.history));
        //alert(JSON.stringify(process.env.PUBLIC_URL));
    }
    render(){
        const st={
            marginTop: '75px'
        }
        const stp={
            color: 'black',
            textIndent: '50px',
            textAlign: 'justify',
            textJustify: 'inter-word'
        }

        const sta={
            color: 'blue',
        }
        return (
        <div>
            <div id="carouselExampleControls" className="carousel slide" data-ride="carousel">
            <div className="carousel-inner">
                <div className="carousel-item active">
                <img className="d-block mx-auto img-fluid" src="https://vasileclaudiu.github.io/reactnoteandtodo/assets/images/carousel/login.jpg" alt="Login Page"/>
                </div>
                <div className="carousel-item">
                <img className="d-block mx-auto img-fluid" src="https://vasileclaudiu.github.io/reactnoteandtodo/assets/images/carousel/dashbord.jpg" alt="Dashbord Page"/>
                </div>
                <div className="carousel-item">
                <img className="d-block mx-auto img-fluid" src="https://vasileclaudiu.github.io/reactnoteandtodo/assets/images/carousel/dashbord-chat.jpg" alt="Dashbord Chat Page"/>
                </div>
            </div>
            <a className="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                <span className="sr-only">Previous</span>
            </a>
            <a className="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                <span className="sr-only">Next</span>
            </a>
            </div>
            <div className="container" style={st}>
                <p style={stp}>Va multumim pentru vizita.</p>

                <div className="mx-auto" style={sta}>&copy; 2019 - React Note and Todo</div>
            </div>
        <CookieConsent
            location="bottom"
            buttonText="Accept cookies!!"
            cookieName="myAwesomeCookieName2"
            style={{ background: "#2B373B" }}
            buttonStyle={{ color: "#4e503b", fontSize: "13px" }}
            expires={150}
        >
            This website uses cookies to enhance the user experience.{" "}
        </CookieConsent>
        </div>
        );
    }
}